<template>
  <div>
    <!---->
    <el-button type="primary" style="float:left;margin-bottom: 20px" @click="clickAdd"></el-button>
  <!---->
  <el-table
    :data="rolePlayerTableData.filter(data => !search || data.role_name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%" size="small" border>
    <el-table-column
      label="id"
      prop="id"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label="id"
      prop="pid"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="pname"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label="id"
      prop="zid"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="zname"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="role_name"
      align="center"
      show-overflow-tooltip
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="user_name"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="is_active"
      align="center"
      :formatter="boolFormatter"
      sortable>
    </el-table-column>
    <el-table-column
      label="id"
      prop="creator_id"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="creator_name"
      align="center"
      min-width="100"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="modify_date"
      align="center"
      :formatter="dateFormatter"
      min-width="150"
      show-overflow-tooltip
      sortable>
    </el-table-column>
    <el-table-column
      min-width="200"
      align="center">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder=""/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="primary"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
    <el-dialog :title="rolePlayerFormTitle" :visible.sync="rolePlayerFormVisible" width="30%" center>
      <!---->
      <el-form ref="rolePlayerForm" :model="rolePlayerForm" :rules="rolePlayerFormRules" label-width="140px" :label-position="rolePlayerFormLabelPosition" style="width: 100%; margin-top: 30px">
        <el-form-item label="" prop="pid">
          <el-select v-model="rolePlayerForm.pid" filterable placeholder="" @change="handleChangePlatform">
            <el-option v-for="(item, index) in platformOptions" :key="index" :label="item.pname" :value="item.pid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="zid">
          <el-select v-model="rolePlayerForm.zid" filterable placeholder="">
            <el-option v-for="(item, index) in zoneOptions" :key="index" :label="item.sname" :value="item.sid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="role_name">
          <el-input v-model="rolePlayerForm.role_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="user_name">
          <el-input v-model="rolePlayerForm.user_name" placeholder=""></el-input>
        </el-form-item>
        <!--<el-form-item label="id" prop="applicant_id" v-show="false">-->
          <!--<el-input v-model="rolePlayerForm.applicant_id" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" prop="amount">-->
          <!--<el-input v-model.number="rolePlayerForm.amount" placeholder="0-999"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="">
          <el-switch v-model="rolePlayerForm.is_active"></el-switch>
        </el-form-item>
        <!--<el-form-item label="()" prop="regular_period" v-show="rolePlayerForm.is_regular">-->
          <!--<el-input v-model.number="rolePlayerForm.regular_period" placeholder="0-999"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item>
          <el-button type="primary" @click="submitForm('rolePlayerForm')"></el-button>
          <el-button type="danger" @click="resetForm('rolePlayerForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import { getRolePlayerList, addRolePlayer, editRolePlayer, deleteRolePlayer, getPlatformOptions, getZoneOptions } from '../../api/api';

    export default {
      name: "RolePlayer",
      data() {
        return {
          rolePlayerTableData: [],
          search: '',
          // 
          platformOptions: [],
          // 
          zoneOptions: [],
          // 
          rolePlayerFormTitle: '',
          // 
          rolePlayerFormVisible: false,
          // 
          rolePlayerFormLabelPosition: 'left',
          // 
          rolePlayerForm: {
            id: '',
            pid: '',
            zid: '',
            role_name: '',
            user_name: '',
            is_active: true,
            creator_id: 0,
          },
          // 
          rolePlayerFormRules: {
            pid: [
              { required: true, message: '', trigger: 'change' }
            ],
            zid: [
              { required: true, message: '', trigger: 'change' }
            ],
            role_name: [
              { required: true, message: ''},
            ],
            user_name: [
              { required: true, message: ''},
            ],
            // amount: [
            //   { required: true, message: '' },
            //   { type: 'number', message: '' },
            //   { pattern:/^\d{1,3}$/, message: '1-999'}
            // ],
            // regular_period: [
            //   { required: true, message: '' },
            //   { type: 'number', message: '' },
            //   { pattern:/^\d{1,3}$/, message: '1-999'}
            // ]
          },
        }
      },
      methods: {
        // 
        alertMessage(message, type) {
          this.$message({
            showClose: true,
            message: message,
            type: type,
          });
        },
        // 
        clickAdd() {
          this.rolePlayerFormTitle = '';
          this.rolePlayerForm.id = '';
          // this.rolePlayerForm.pid = '';
          // this.rolePlayerForm.zid = '';
          this.rolePlayerFormVisible = true;
        },
        // 
        handleEdit(index, row) {
          this.rolePlayerFormTitle = '';
          this.rolePlayerFormVisible = true;
          this.rolePlayerForm.id = row.id;
          this.rolePlayerForm.pid = row.pid;
          this.rolePlayerForm.zid = row.zid;
          this.rolePlayerForm.role_name = row.role_name;
          this.rolePlayerForm.user_name = row.user_name;
          this.rolePlayerForm.is_active = row.is_active;
        },
        handleDelete(index, row) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            deleteRolePlayer(row.id).then(res => {
              if (res.status === 204) {
                this.rolePlayerTableData = this.rolePlayerTableData.filter(item => item.id !== row.id);
                this.alertMessage('', 'success');
              }
            }).catch(error => {

            });
          }).catch(() => {

          });
        },
        // 
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              // console.log(sessionStorage.getItem('userId'));
              this.rolePlayerForm.creator_id = Number(sessionStorage.getItem('userId'));
              // idid
              if (this.rolePlayerForm.id) {
                editRolePlayer(this.rolePlayerForm).then(res => {
                  // 
                  if (res.status === 202) {
                    this.alertMessage(res.data, 'warning');
                  } else {
                    Object.assign(this.rolePlayerTableData.find(item => item.id === this.rolePlayerForm.id), res.data);
                    this.alertMessage('', 'success');
                    this.rolePlayerFormVisible = false;
                  }
                })
              } else {
                addRolePlayer(this.rolePlayerForm).then(res => {
                  if (res.status === 201) {
                    this.rolePlayerTableData.push(res.data);
                    this.alertMessage('', 'success');
                    this.rolePlayerFormVisible = false;
                  } else if(res.status === 202) {
                    this.alertMessage(res.data, 'warning');
                  }
                }).catch(error => {
                  this.alertMessage(error.message, 'error');
                });
              }
            }
          });
        },
        // 
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
        // 
        handleChangePlatform(value) {
          let platformId = value;
          // 
          getZoneOptions().then(res => {
            let data = res.data;
            this.zoneOptions = data.filter(item => item.gid ===50 && item.pid === platformId);
          })
        },
        // 
        dateFormatter(row, column, cellValue, index) {
          return cellValue ? cellValue.replace('T', ' ') : '/';
        },
        // 
        boolFormatter(row, column, cellValue, index) {
          return cellValue ? '' : '';
        },
        // 
        nullFormatter(row, column, cellValue, index) {
          return cellValue ? cellValue : '/';
        },
        // 
        applyStatusFormatter(row, column, cellValue, index) {
          if (cellValue === 0) {
            return '';
          } else if (cellValue === 1) {
            return '';
          } else if (cellValue === 2) {
            return '';
          }
        }
      },
      created() {
        getRolePlayerList().then(res => {
          this.rolePlayerTableData = res.data;
        }).catch(error => {

        });
        getPlatformOptions().then(res => {
          let data = res.data;
          this.platformOptions = data.filter(item => item.gid === 50 && item.pid > 0);
        }).catch(error => {

        });
      },
    }
</script>

<style scoped>

</style>



