<template>
    <div>
      <el-form ref="queryForm" :inline="true" :rules="queryFormRules" :model="queryForm" style="float: left;">
        <el-form-item label="" prop="cid">
          <el-select v-model="queryForm.cid" clearable placeholder="" @change="handleChangeChannel">
            <el-option v-for="(item, index) in channelOptions" :key="index" :label="item.cname" :value="item.cid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="queryForm.appid" clearable placeholder="" @change="handleChangePackage">
            <el-option v-for="(item, index) in packageOptions" :key="index" :label="item.appid" :value="item.appid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('queryForm')"></el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="serverManagementTableData.filter(data => !search || data.zonename.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%"
        ref="serverManagementTableData"
        size="small"
        border
        :row-key="getRowKeys"
        @selection-change="handleSelectionChange">
        <el-table-column
          label="id"
          prop="id"
          align="center"
          v-if="false">
        </el-table-column>
        <el-table-column
          label="id"
          prop="GID"
          align="center"
          v-if="false">
        </el-table-column>
        <el-table-column
          label="id"
          prop="CID"
          align="center"
          v-if="false">
        </el-table-column>
        <el-table-column
          label="id"
          prop="PID"
          align="center"
          v-if="false">
        </el-table-column>
        <el-table-column
          label=""
          prop="zonename"
          align="center"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="statu"
          align="center"
          sortable
          v-if="false">
        </el-table-column>
        <el-table-column
          label=""
          prop="server_statu"
          align="center"
          :formatter="serverStatuFormatter"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="server_suggest"
          align="center"
          :formatter="serverSuggestFormatter"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="is_delete"
          align="center"
          sortable
          v-if="false">
        </el-table-column>
        <el-table-column
          label="idx"
          prop="zoneidx"
          align="center"
          sortable
          v-if="false">
        </el-table-column>
        <el-table-column
          label=""
          prop="OpenDate"
          align="center"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="MergeDate"
          align="center"
          sortable>
        </el-table-column>
        <el-table-column
          label="id"
          prop="MergeID"
          align="center"
          sortable>
        </el-table-column>
        <el-table-column
          label="appid"
          prop="appid"
          align="center"
          min-width="100"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="open_type"
          align="center"
          min-width="100"
          :formatter="openTypeFormatter"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="open_time"
          align="center"
          min-width="120"
          :formatter="openTypeTimeFormatter"
          sortable>
        </el-table-column>
        <el-table-column
          label=""
          prop="max_user"
          align="center"
          min-width="100"
          sortable>
        </el-table-column>
        <!--<el-table-column-->
          <!--label=""-->
          <!--prop="open_type_value"-->
          <!--align="center"-->
          <!--min-width="100"-->
          <!--:formatter="openTypeValueFormatter"-->
          <!--sortable>-->
        <!--</el-table-column>-->
        <el-table-column
          min-width="200"
          align="center">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope" v-if="scope.row.open_type === null">
            <el-button
              size="mini"
              type="danger"
              @click="openNow(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
  import { getServerManagementList, getChannelList, getAppPackageList, openNow } from '../../api/api';

    export default {
      name: "ZoneList",
      data() {
        return {
          // 
          channelOptions: [],
          // 
          packageOptions: [],
          // 
          recommendOptions: [
            {value: 0, label: ''},
            {value: 1, label: ''},
          ],
          openTypeOptions: [
            {value: 0, label: ''},
            {value: 1, label: ''},
            {value: 2, label: ''}
          ],
          // 
          queryForm: {
            gid: 50,
            cid: '',
            appid: '',
          },
          // 
          queryFormRules: {
            cid: [
              { required: true, message: '', trigger: 'change' }
            ],
          },
          // 
          openFormLabelPosition: 'left',
          // 
          openFormVisible: false,
          // 
          openForm: {
            // id: 0,
            // zoneidx: '',
            open_type: 0,
            max_users: 0,
            autoOpenTime: 0,
            // server_suggest: 0,
            // max_users: '',
            // server_weight: '',
            // autoOpenTime: '',
            by_zone: false,
          },
          // 
          openFormRules: {
            max_users: [
              // { required: true, message: '' },
              { type: 'number', message: '' },
              { pattern:/^\d{1,4}$/, message: '1-9999'}
            ],
            // // server_weight: [
            // //   { required: true, message: '' },
            // //   { type: 'number', message: '' },
            // //   { pattern:/^\d$/, message: '1-9'}
            // // ],
            autoOpenTime: [
              // { type: 'date', required: true, message: ''}
              { type: 'date', message: ''}
            ],
          },

          // 
          editFormLabelPosition: 'left',
          // 
          editFormVisible: false,
          // 
          editForm: {
            id: 0,
            zoneidx: '',
            server_suggest: 0,
            max_users: '',
            server_weight: '',
            autoOpenTime: '',
            by_zone: false,
          },
          // 
          editFormRules: {
            max_users: [
              { required: true, message: '' },
              { type: 'number', message: '' },
              { pattern:/^\d{1,4}$/, message: '1-9999'}
            ],
            server_weight: [
              { required: true, message: '' },
              { type: 'number', message: '' },
              { pattern:/^\d$/, message: '1-9'}
            ],
            autoOpenTime: [
              { type: 'date', required: true, message: ''}
            ],
          },
          // 
          serverManagementTableData: [],
          search: '',
          // 
          multipleSelection: [],
          // 
          byUser: false,
          // 
          byTime: false,
        }
      },
      methods: {
        // 
        alertMessage(message, type) {
          this.$message({
            showClose: true,
            message: message,
            type: type,
          });
        },
        // 
        openTypeFormatter(row, column, cellValue, index) {
          let text = '';
          switch (cellValue) {
            case 0:
              text = '';
              break;
            case 1:
              text = '';
              break;
            case 2:
              text = '';
              break;
            default:
              text = '';
              break;
          }
          return text
        },
        // 
        openTypeTimeFormatter(row, column, cellValue, index) {
          // let text = '';
          if (cellValue !== null) {
            return this.timeStampFormatter('', '', cellValue, '')
          }
        },
        // 
        timeStampFormatter(row, column, cellValue, index) {
          let date = new Date(cellValue * 1000);
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
          let D = date.getDate() + ' ';
          let h = date.getHours() + ':';
          let m = date.getMinutes() + ':';
          let s = date.getSeconds();
          return Y + M + D + h + m + s;
        },
        // 
        serverSuggestFormatter(row, column, cellValue, index) {
          let text = '';
          switch (cellValue) {
            case 0:
              text = '';
              break;
            case 1:
              text = '';
              break;
            default:
              text = '';
              break;
          }
          return text
        },
        // 
        serverStatuFormatter(row, column, cellValue, index) {
          let text = '';
          switch (cellValue) {
            case 101:
              text = '';
              break;
            case 102:
              text = '';
              break;
            case 103:
              text = '';
              break;
            case 104:
              text = '';
              break;
            case 105:
              text = '';
              break;
            case 111:
              text = '';
              break;
            case 112:
              text = '';
              break;
            case 113:
              text = '';
              break;
            case 201:
              text = '';
              break;
            case 202:
              text = '';
              break;
            default:
              text = '';
              break;
          }
          return text
        },
        getRowKeys(row) {
          return row.id
        },
        // 
        handleSelectionChange(value) {
          this.multipleSelection = value;
        },
        // 
        clearSelect() {
          this.multipleSelection = [];
        },
        // 
        handleOpen() {
          if (this.multipleSelection.length < 1) {
            this.alertMessage('', 'warning');
            return false
          } else {
            this.openFormVisible = true;
          }
          // this.openForm.id = row.id;
          // this.openForm.zoneidx = row.zoneidx;
          // this.openForm.type = row.type;
          // this.editForm.server_suggest = row.server_suggest;
          // this.openForm.max_users = row.max_users;
          // this.editForm.server_weight = row.server_weight;
          // this.openForm.autoOpenTime = new Date(this.timeStampFormatter('', '', row.autoOpenTime, ''));
        },
        // 
        changeOpenType(value) {
          this.openForm.max_users = 0;
          this.openForm.autoOpenTime = 0;
          switch (value) {
            case 0:
              this.byUser = this.byTime =false;
              break;
            case 1:
              this.byUser = true;
              this.byTime = !this.byUser;
              break;
            case 2:
              this.byUser = false;
              this.byTime = !this.byUser;
              break;
          }
        },
        // 
        handleChangeChannel(value) {
          if (!value) {
            // 
            this.packageOptions = [];
            this.queryForm.appid = '';
            return false
          }
          getAppPackageList().then(res => {
            this.packageOptions = res.data;
          }).catch(error => {

          });
        },
        // 
        handleChangePackage(value) {
          // console.log(value)
        },
        // 
        onSubmit(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              getServerManagementList(this[formName]).then(res => {
                let data = res.data;
                // console.log(data)
                this.serverManagementTableData = data;
                // this.serverManagementTableData = data.map(item => Object.assign(
                //   item, {
                //     'autoOpenTime': this.timeStampToDateTime(item.autoOpenTime),
                //     // 'server_suggest': this.serverSuggestToStr(item.server_suggest),
                //     'server_statu': this.serverStatuToStr(item.server_statu),
                //   }
                // ));
              }).catch(error => {
                // console.log(error)
              });
            }
            else {
              // console.log('shibai2')
            }
          });
        },
        // 
        openNow(index, row) {
          if (row.open_type !== null) {
            this.alertMessage('', 'error');
            return false
          }
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            openNow(row).then(res => {
              // console.log(res.data)
              Object.assign(this.serverManagementTableData.find(item => item.id === row.id), res.data)
              // this.serverManagementTableData.find(item => item.id === row.id)
              // this.serverManagementTableData = (this.serverManagementTableData.filter(item => item.id !== row.id));
              // console.log(res);
              this.alertMessage('', 'success');
            }).catch(error => {
              // console.log(error)
              this.alertMessage('', 'error');
            });
          }).catch(() => {

          });
        },
        // 
        // submitForm(formName) {
        //   this.$refs.serverManagementTableData.clearSelection();
        //   this.$refs[formName].validate((valid) => {
        //     this[formName].autoOpenTime = this[formName].autoOpenTime / 1000;
        //     if (valid) {
        //       updateAppServerChannel(this[formName]).then(res => {
        //         let data = res.data;
        //         console.log(data);
        //         // this.serverManagementTableData
        //         if (this[formName].by_zone) {
        //           // Object.assign(this.serverManagementTableData.find(item => item.id === this[formName].id), res.data);
        //           Object.assign(this.serverManagementTableData.find(item => item.id === this[formName].id), res.data.find(item => item.id === this[formName].id));
        //           this.alertMessage('', 'success');
        //         } else {
        //           Object.assign(this.serverManagementTableData.find(item => item.id === this[formName].id), res.data);
        //           this.alertMessage('', 'success');
        //         }
        //       }).catch(error => {
        //         this.alertMessage('', 'error');
        //       });
        //       this.editFormVisible = false;
        //     } else {
        //       console.log('shibai')
        //     }
        //   });
        // },

        // 
        handleClickSelectPackage(value) {
          if (!value) {
            // 
            this.packageOptions = [];
            this.queryForm.appid = '';
            // this.serverManagementTableData =[];
            return false
          }

          getAppPackageList().then(res => {
            // console.log('pac')
            let data = res.data;
            // this.packageOptions = data.reduce((all, next) => all.some(item => item.appid === next.appid) ? all : [...all, next], [])
            this.packageOptions = data;
          }).catch(error => {
            // console.log(error)
          });
        }
      },
      created() {
        getChannelList().then(res => {
          // let data = res.data;
          this.channelOptions = res.data;
          // this.queryForm.cid = this.channelOptions[0].cid;
          // getServerManagementList(this.queryForm).then(res => {
          //   let data = res.data;
          //   console.log(data)
          //   this.serverManagementTableData = data;
          // }).catch(error => {
          //   console.log(error)
          // });
        }).catch(error => {
          // console.log(error)
        });
      }
    }
</script>

<style scoped>

</style>



