<template>
  <!---->
  <el-table
    :data="examineTableData.filter(data => !search || data.role_name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%" size="small" border>
    <el-table-column
      label="id"
      prop="id"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label="id"
      prop="pid"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="pname"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label="id"
      prop="zid"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="zname"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="role_name"
      align="center"
      show-overflow-tooltip
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="is_regular"
      align="center"
      :formatter="boolFormatter"
      min-width="100"
      sortable>
    </el-table-column>
    <el-table-column
      label="()"
      prop="regular_period"
      align="center"
      :formatter="nullFormatter"
      min-width="120"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="amount"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      label="id"
      prop="applicant_id"
      align="center"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="applicant_name"
      align="center"
      min-width="100"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="created_date"
      align="center"
      :formatter="dateFormatter"
      min-width="150"
      show-overflow-tooltip
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="status"
      align="center"
      :formatter="applyStatusFormatter"
      sortable>
    </el-table-column>
    <el-table-column
      label="id"
      prop="approver_id"
      align="center"
      :formatter="nullFormatter"
      v-if="false">
    </el-table-column>
    <el-table-column
      label=""
      prop="approver_name"
      align="center"
      :formatter="nullFormatter"
      min-width="100"
      sortable>
    </el-table-column>
    <el-table-column
      label=""
      prop="approve_date"
      align="center"
      :formatter="dateFormatter"
      min-width="150"
      show-overflow-tooltip
      sortable>
    </el-table-column>
    <el-table-column
      min-width="200"
      align="center">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder=""/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="primary"
          @click="handleExamine(scope.$index, scope.row, 1)"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleExamine(scope.$index, scope.row, 2)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import { getWelfareList, modifyWelfare } from '../../api/api';

    export default {
      name: "Examine",      data() {
        return {
          examineTableData: [],
          search: '',
        }
      },

      methods: {
        // 
        alertMessage(message, type) {
          this.$message({
            showClose: true,
            message: message,
            type: type,
          });
        },
        // 
        handleExamine(index, row, status) {
          if (row.status !== 0) {
            this.alertMessage('', 'warning');
            return false
          }
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            // json
            Object.assign(row, {'status': status, 'approver_id': Number(sessionStorage.getItem('userId'))});
            modifyWelfare(row).then(res => {
              // Object.assign(this.examineTableData.find(item => item.id === row.id), res.data);
              this.examineTableData = this.examineTableData.filter(item => item.id !== res.data.id);
            }).catch(error => {

            });
            // 
            if (status === 1) {
              this.alertMessage('', 'success');
            } else if (status === 2) {
              this.alertMessage('', 'success');
            }
          }).catch(() => {

          });
        },
        // 
        dateFormatter(row, column, cellValue, index) {
          return cellValue ? cellValue.replace('T', ' ') : '/';
        },
        // 
        boolFormatter(row, column, cellValue, index) {
          return cellValue ? '' : '';
        },
        // 
        nullFormatter(row, column, cellValue, index) {
          return cellValue ? cellValue : '/';
        },
        // 
        applyStatusFormatter(row, column, cellValue, index) {
          if (cellValue === 0) {
            return '';
          } else if (cellValue === 1) {
            return '';
          } else if (cellValue === 2) {
            return '';
          }
        }
      },
      created() {
        getWelfareList().then(res => {
          this.examineTableData = res.data.filter(item => item.status === 0);
        }).catch(error => {

        });
      },
    }
</script>

<style scoped>

</style>



