// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import store from './vuex/store'
import { Message } from 'element-ui';


Vue.use(ElementUI);
Vue.prototype.$axios = axios;
Vue.config.productionTip = false;

// http request
axios.interceptors.request.use(
  config => {
  if (sessionStorage.getItem('userToken')) {
    config.headers.Authorization = sessionStorage.getItem('userToken');
  }
    return config;
  },
    error => {
    return Promise.reject(error);
  }
);

// http response
axios.interceptors.response.use(
  response => {
    // console.log(response);
  return response;
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400: error.message = '(400)' ; break;
        case 401: error.message = '(401)'; break;
        case 403:
          error.message = '(403)';
          router.push({path: '/login'});
          Message({
            showClose: true,
            message: 'token, ',
            type: 'error'
          });
          break;

        case 404: error.message = '(404)'; break;
        case 408: error.message = '(408)'; break;
        case 500: error.message = '(500)'; break;
        case 501: error.message = '(501)'; break;
        case 502: error.message = '(502)'; break;
        case 503: error.message = '(503)'; break;
        case 504: error.message = '(504)'; break;
        case 505: error.message = 'HTTP(505)'; break;
        default: error.message = `(${error.response.status})!`;
      }
    } else {
      error.message = '!'
    }
    return Promise.reject(error)
  }
);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});



