<template>
  <div>
    <!---->
    <el-button type="primary" style="float:left;margin-bottom: 20px" @click="clickAdd"></el-button>
    <!---->
    <el-table
      :data="applyTableData.filter(data => !search || data.role_name.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%" size="small" border>
      <el-table-column
        label="id"
        prop="id"
        align="center"
        v-if="false">
      </el-table-column>
      <el-table-column
        label="id"
        prop="pid"
        align="center"
        v-if="false">
      </el-table-column>
      <el-table-column
        label=""
        prop="pname"
        align="center"
        sortable>
      </el-table-column>
      <el-table-column
        label="id"
        prop="zid"
        align="center"
        v-if="false">
      </el-table-column>
      <el-table-column
        label=""
        prop="zname"
        align="center"
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="role_name"
        align="center"
        show-overflow-tooltip
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="is_regular"
        align="center"
        :formatter="boolFormatter"
        min-width="100"
        sortable>
      </el-table-column>
      <el-table-column
        label="()"
        prop="regular_period"
        align="center"
        :formatter="nullFormatter"
        min-width="120"
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="amount"
        align="center"
        sortable>
      </el-table-column>
      <el-table-column
        label="id"
        prop="applicant_id"
        align="center"
        v-if="false">
      </el-table-column>
      <el-table-column
        label=""
        prop="applicant_name"
        align="center"
        min-width="100"
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="created_date"
        align="center"
        :formatter="dateFormatter"
        min-width="150"
        show-overflow-tooltip
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="status"
        align="center"
        :formatter="applyStatusFormatter"
        sortable>
      </el-table-column>
      <el-table-column
        label="id"
        prop="approver_id"
        align="center"
        :formatter="nullFormatter"
        v-if="false">
      </el-table-column>
      <el-table-column
        label=""
        prop="approver_name"
        align="center"
        :formatter="nullFormatter"
        min-width="100"
        sortable>
      </el-table-column>
      <el-table-column
        label=""
        prop="approve_date"
        align="center"
        :formatter="dateFormatter"
        min-width="150"
        show-overflow-tooltip
        sortable>
      </el-table-column>
      <el-table-column
        min-width="200"
        align="center">
        <template slot="header" slot-scope="scope">
          <el-input
            v-model="search"
            size="mini"
            placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="newApplyFormVisible" width="30%" center>
    <!---->
    <el-form ref="newApplyForm" :model="newApplyForm" :rules="newApplyFormRules" label-width="140px" :label-position="newApplyFormLabelPosition" style="width: 100%; margin-top: 30px">
      <el-form-item label="" prop="pid">
        <el-select v-model="newApplyForm.pid" filterable placeholder="" @change="handleChangePlatform">
          <el-option v-for="(item, index) in platformOptions" :key="index" :label="item.pname" :value="item.pid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="zid">
        <el-select v-model="newApplyForm.zid" filterable placeholder="" @change="handleChangeZone">
          <el-option v-for="(item, index) in zoneOptions" :key="index" :label="item.sname" :value="item.sid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="role_name">
        <el-select v-model="newApplyForm.role_name" filterable placeholder="">
          <el-option v-for="(item, index) in roleNameOptions" :key="index" :label="item.role_name" :value="item.role_name"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="id" prop="applicant_id" v-show="false">
        <el-input v-model="newApplyForm.applicant_id" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="amount">
        <el-input v-model.number="newApplyForm.amount" placeholder="0-999"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="newApplyForm.is_regular" @change="isRegularChange"></el-switch>
      </el-form-item>
      <el-form-item label="()" prop="regular_period" v-show="newApplyForm.is_regular">
        <el-input v-model.number="newApplyForm.regular_period" placeholder="0-999"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('newApplyForm')"></el-button>
        <el-button type="danger" @click="resetForm('newApplyForm')"></el-button>
      </el-form-item>
    </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import { getPlatformOptions, getZoneOptions, getRolePlayerList, getWelfareList, addWelfare, deleteWelfare } from '../../api/api';

  export default {
    name: "Apply",
    data() {
      return {
        // 
        platformOptions: [],
        // 
        zoneOptions: [],
        // 
        roleNameOptions: [],
        // 
        newApplyForm: {
          pid: '',
          zid: '',
          role_name: '',
          applicant_id: 0,
          amount: '',
          is_regular: false,
          regular_period: 0,
        },
        // 
        newApplyFormRules: {
          pid: [
            { required: true, message: '', trigger: 'change' }
          ],
          zid: [
            { required: true, message: '', trigger: 'change' }
          ],
          role_name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          amount: [
            { required: true, message: '' },
            { type: 'number', message: '' },
            { pattern:/^\d{1,3}$/, message: '1-999'}
          ],
          regular_period: [
            { required: true, message: '' },
            { type: 'number', message: '' },
            { pattern:/^\d{1,3}$/, message: '1-999'}
          ]
        },
        // 
        newApplyFormLabelPosition: 'left',
        // 
        newApplyFormVisible: false,
        applyTableData: [],
        search: '',
      }
    },
    methods: {
      // 
      alertMessage(message, type) {
        this.$message({
          showClose: true,
          message: message,
          type: type,
        });
      },
      // 
      clickAdd() {
        this.newApplyFormVisible = true;
      },
      isRegularChange(value) {
        this.newApplyForm.regularPeriod = 0;
      },
      // 
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // console.log(sessionStorage.getItem('userId'));
            this.newApplyForm.applicant_id = Number(sessionStorage.getItem('userId'));
            addWelfare(this.newApplyForm).then(res => {
              if (res.status === 201) {
                this.applyTableData.push(res.data);
                this.alertMessage('', 'success');
              }
            }).catch(error => {
              this.alertMessage(error.message, 'error');
            });
            this.newApplyFormVisible = false;
          }
        });
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      // 
      handleChangePlatform(value) {
        // 
        getZoneOptions().then(res => {
          let data = res.data;
          this.zoneOptions = data.filter(item => item.gid === 50 && item.pid === this.newApplyForm.pid);
        })
      },
      handleChangeZone(value) {
        // 
        getRolePlayerList().then(res => {
          let data = res.data;
          // console.log(data)
          this.roleNameOptions = data.filter(item => item.pid === this.newApplyForm.pid && item.zid === this.newApplyForm.zid)
        });
      },
      handleDelete(index, row) {
        if (row.status !== 0) {
          this.alertMessage('', 'warning');
          return false
        }
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteWelfare(row.id).then(res => {
            if (res.status === 204) {
              this.applyTableData = this.applyTableData.filter(item => item.id !== row.id);
              this.alertMessage('', 'success');
            } else if (res.status === 202) {
              this.alertMessage(res.data, 'warning')
            }
          }).catch(error => {
            this.alertMessage(error.message, 'error');
          })
        }).catch(() => {

        });
      },
      // 
      dateFormatter(row, column, cellValue, index) {
        return cellValue ? cellValue.replace('T', ' ') : '/';
      },
      // 
      boolFormatter(row, column, cellValue, index) {
       return cellValue ? '' : '';
      },
      // 
      nullFormatter(row, column, cellValue, index) {
        return cellValue ? cellValue : '/';
      },
      // 
      applyStatusFormatter(row, column, cellValue, index) {
        if (cellValue === 0) {
          return '';
        } else if (cellValue === 1) {
          return '';
        } else if (cellValue === 2) {
          return '';
        }
      }
    },
    created() {
      getWelfareList().then(res => {
        let data = res.data;
        this.applyTableData = data.filter(item => item.applicant_id === Number(sessionStorage.getItem('userId')) && item.status === 0);
      }).catch(error => {

      });
      getPlatformOptions().then(res => {
        let data = res.data;
        this.platformOptions = data.filter(item => item.gid === 50 && item.pid > 0);
      }).catch(error => {

      });
    }
  }

</script>

<style scoped>

</style>



