<template>
  <div style="float: left">
    <el-form ref="queryForm" :inline="true" :model="queryForm" :rules="queryFormRules">
      <el-form-item label="" prop="cid">
        <el-select v-model="queryForm.cid" clearable placeholder="" @change="handleChangeChannel">
          <el-option v-for="(item, index) in channelOptions" :key="index" :label="item.cname" :value="item.cid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="appid">
        <el-select v-model="queryForm.appid" clearable placeholder="">
          <el-option v-for="(item, index) in packageOptions" :key="index" :label="item.appid" :value="item.appid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onQuery('queryForm')"></el-button>
        <!--<el-button type="primary" @click="onQuery"></el-button>-->
      </el-form-item>
    </el-form>

    <el-form ref="planForm" :model="planForm" :inline="true">
      <div v-for="(plan, index) in planForm.plans" :key="plan.key">
        <!--<el-form-item label="id">-->
          <!--<el-input readonly v-model="plan.id"></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="zoneidx">-->
          <!--<el-input readonly v-model="plan.zoneidx"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item>
          <el-tag type="success" v-show="plan.planned"></el-tag>
          <el-tag type="danger" v-show="!plan.planned"></el-tag>
        </el-form-item>
        <el-form-item label="" :prop="'plans.' + index + '.id'">
          <el-input readonly v-model="plan.zonename"></el-input>
          <!--<el-select v-model="plan.id" placeholder="" @change="handleChangeZone(plan)">-->
            <!--<el-option v-for="(item, index) in zoneOptions" :key="index" :label="item.zonename" :value="item.id"></el-option>-->
          <!--</el-select>-->
        </el-form-item>
        <el-form-item label="" :prop="'plans.' + index + '.open_time'">
          <el-date-picker
            v-model="plan.open_time"
            type="datetime"
            value-format="timestamp"
            placeholder=""
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" :prop="'plans.' + index + '.max_user'">
          <el-input v-model.number="plan.max_user" placeholder=""></el-input>
        </el-form-item>
        <!--<el-form-item label="" :prop="'plans.' + index + '.open_type'" :rules="[-->
      <!--{ required: true, message: '', trigger: 'change' },-->
    <!--]">-->
          <!--<el-select v-model="plan.open_type" clearable placeholder="" @change="handleChangeOpenType(plan)">-->
            <!--<el-option v-for="(item, index) in openTypeOptions" :key="index" :label="item.label" :value="item.value"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" :prop="'plans.' + index + '.open_time'" :rules="[-->
          <!--{ required: true, message: '', trigger: 'change' },-->
        <!--]">-->
          <!--<el-date-picker-->
            <!--v-model="plan.open_time"-->
            <!--type="datetime"-->
            <!--value-format="timestamp"-->
            <!--placeholder="">-->
          <!--</el-date-picker>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" :prop="'plans.' + index + '.max_user'" :rules="[-->
              <!--{ required: true, message: '', trigger: 'change' },-->
        <!--]">-->
          <!--<el-input v-model.number="plan.max_user" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="">
          <el-switch v-model="plan.by_zone" @change="changeSwitch(plan)"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.prevent="savePlan('planForm', plan)"></el-button>
          <!--<el-button type="danger" @click.prevent="removePlan(plan)"></el-button>-->
        </el-form-item>

      </div>
      <!--<el-form-item label="">-->
      <!--<el-switch v-model="planForm.by_zone"></el-switch>-->
      <!--</el-form-item>-->
    <el-form-item style="float: left">
      <el-button type="primary" @click="addPlan"></el-button>
      <!--<el-button type="primary" @click="submitForm('planForm')"></el-button>-->
    </el-form-item>
  </el-form>
  </div>
</template>

<script>
import { getServerManagementList, getChannelList, getAppPackageList, setOpenPlan, deleteOpenPlan } from '../../api/api';
  export default {
      name: "OpenZone",
      data() {
        return {
          // 
          channelOptions: [],
          // 
          packageOptions: [],
          // 
          zoneOptions: [],
          // 
          recommendOptions: [
            {value: 0, label: ''},
            {value: 1, label: ''},
          ],
          openTypeOptions: [
            // {value: 0, label: ''},
            {value: 1, label: ''},
            {value: 2, label: ''}
          ],
          // 
          queryForm: {
            gid: 50,
            cid: '',
            appid: '',
          },
          // 
          queryFormRules: {
            cid: [
              { required: true, message: '', trigger: 'change' },
            ],
            appid: [
              { required: true, message: '', trigger: 'change' },
            ],
          },
          // 
          planForm: {
            gid: 50,
            cid: '',
            appid: '',
            plans: [
              // {
              // id: '',
              // zoneidx: '',
              // zonename: '',
              // open_type: '',
              // max_user: '',
              // open_time: '',
              // by_zone: false,
              // }
            ],
          },
          // // 
          pickerOptions: {
            disabledDate(time) {
              let nowDate = new Date();
              return time.getTime() < new Date(nowDate.setDate(nowDate.getDate() - 1));
            }
          }
        }
      },
    methods: {
      // 
      alertMessage(message, type) {
        this.$message({
          showClose: true,
          message: message,
          type: type,
        });
      },
      // 
      handleChangeChannel (value) {
        if (!value) {
          // 
          this.packageOptions = [];
          this.queryForm.appid = '';
          return false
        }
        // 
        getAppPackageList().then(res => {
          let data = res.data;
          // this.packageOptions = data.reduce((all, next) => all.some(item => item.appid === next.appid) ? all : [...all, next], [])
          this.packageOptions = data;
        }).catch(error => {

        });
      },
      // 
      changeSwitch(plan) {
        let zone = this.zoneOptions.find(item => item.id === plan.id);
        if (!zone.sync) {
          this.alertMessage('', 'error');
          plan.by_zone = false;
        }
      },
      // 
      addPlan() {
        // console.log(this.planForm.plans)
        // console.log(this.zoneOptions)
        // 
        if (this.planForm.plans.length >= this.zoneOptions.length) {
          this.alertMessage('', 'warning')
        } else {
          if (this.planForm.plans.length >= 1) {
            // 
            let previousPlan = this.planForm.plans[this.planForm.plans.length - 1];
            if (!previousPlan.planned) {
              this.alertMessage('', 'error')
              return false
            }
          }
          let plan = this.zoneOptions.filter(item => !(item.open_time || item.max_user))[0];
          // console.log(this.planForm)
          // let selectedArray = this.planForm.plans.map(item => item.id);
          // console.log(selectedArray)
          this.planForm.plans.push({
            'id': plan.id,
            'zoneidx': plan.zoneidx,
            'zonename': plan.zonename,
            'open_type': plan.open_type,
            'max_user': plan.max_user,
            'open_time': plan.open_time,
            'by_zone': plan.sync,
            'planned': false,
            key: Date.now()
            // 'id': '',
            // 'zoneidx': '',
            // 'zonename':'',
            // 'open_type': '',
            // 'max_user': '',
            // 'open_time': '',
            // 'planned': false,
            // key: Date.now()
          });
        }
      },
      // 
      savePlan(formName, plan) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              // 
              // plan.open_time /= 1000;
              if (plan.open_time || plan.max_user) {
                // if (plan.open_time !== null && plan.open_time < Date.now() + 60 * 60 * 1000) {
                //   this.alertMessage('1', 'error');
                //   return false
                // }
                setOpenPlan(plan).then(res => {
                  if (res.status === 204) {
                    this.alertMessage(',', 'error');
                    return false
                  }
                  let data = res.data;
                  plan.planned = true;
                  // 
                  // plan.open_time = data.find(item => item.id === plan.id).open_time * 1000;
                  // console.log(data)
                  this.alertMessage('', 'success');
                }).catch(error => {
                  this.alertMessage('', 'error');
                });
              } else {
                this.alertMessage('', 'error');
                return false
              }
            }).catch(() => {

            });
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      // 
      removePlan(plan) {
        // console.log(plan)
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteOpenPlan(plan).then(res => {
            this.planForm.plans.pop(this.planForm.plans.find(item => item.id === plan.id));
            this.alertMessage('', 'success');
          }).catch(error => {
            this.alertMessage('', 'error');
          });
        }).catch(() => {

        });
        // let index = this.planForm.plans.indexOf(plan);
        // if (index !== -1) {
        //   this.planForm.plans.splice(index, 1);
        // }
      },
      handleChangeZone(plan) {
        // console.log(plan)
        if (this.planForm.plans.filter(item => item.id === plan.id).length > 1) {
          this.alertMessage('', 'error')
          plan.id = '';
        } else {
          plan.zoneidx = this.zoneOptions.find(item => item.id === plan.id).zoneidx;
          plan.zonename = this.zoneOptions.find(item => item.id === plan.id).zonename;
        }
        // console.log(plan)
      },
      // 
      handleChangeOpenType(plan) {
        if (plan.open_type === 1) {
          plan.open_time = plan.open_time ? plan.open_time : 0;
          // console.log(plan.open_time)
          // this.planForm.plans.find(item => item.id === plan.id).open_time = 0;
        } else if (plan.open_type === 2) {
          plan.max_user = plan.max_user ? plan.max_user : 0;
          // this.planForm.plans.find(item => item.id === plan.id).max_user = 0;
        }
        // if (value === 1) {
        //
        // }
      },
      // 
      onQuery(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            getServerManagementList(this.queryForm).then(res => {
              let data = res.data;
              this.zoneOptions = data.filter(item => item.server_statu > 200);
              // console.log(this.zoneOptions)
              // this.zoneOptions = data.map(item => {
              //   return item;
              // });
              // // 
              // this.plannedZone = data.filter(item => item.open_type !== null);
              // // 
              // this.unplannedZone = data.filter(item => item.open_type === null);
              // 
              this.planForm.plans = [];
              // this.planForm.plans = this.zoneOptions.filter(item => item.open_time || item.max_user);
              // this.planForm.plans.map(item => item.planned = true);
              this.zoneOptions.map(item => {
                if (item.open_time || item.max_user) {
                  this.planForm.plans.push(
                    {
                      'id': item.id,
                      'zoneidx': item.zoneidx,
                      'zonename': item.zonename,
                      'open_type': item.open_type,
                      'open_time': item.open_time * 1000,
                      'max_user': item.max_user,
                      'by_zone': item.sync,
                      'planned': true
                    }
                  )
                }
              });
              // console.log(this.planForm.plans)
            }).catch(error => {
              // console.log(error)
            });
            // console.log(this.planForm.plans)
          } else {
            return false;
          }
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
            // console.log(this[formName])
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      // submitForm() {
      //   this.planForm.appid = this.queryForm.appid;
      //   this.planForm.cid = this.queryForm.cid;
      //   this.planForm.plans.map(item => item.open_time /= 1000);
      //   console.log(this.planForm)
      // }
  },
  created() {
    getChannelList().then(res => {
      // let data = res.data;
      this.channelOptions = res.data;
      // this.queryForm.cid = this.channelOptions[0].cid;
      // getServerManagementList(this.queryForm).then(res => {
      //   let data = res.data;
      //   console.log(data)
      //   this.serverManagementTableData = data;
      // }).catch(error => {
      //   console.log(error)
      // });
    }).catch(error => {
      // console.log(error)
    });
  }
  }
</script>

<style scoped>

</style>



